<template>
  <input id="autocomplete" type="text" v-bind:value="markerAddress" v-on:keyup.enter.prevent='autoComplete'>
</template>
<style>
  .pac-container {
    z-index: 9999999;
  }
</style>
<script>
  import GoogleMapsLoader from '../helpers/GoogleMaps'
  import getJSON from '../helpers/JsonRequest';

  export default {
    props: ['value'],
    data: function () {
      return {
        markerAddress: ''
      }
    },
    mounted() {
      GoogleMapsLoader.load((google) => {
        var autocomplete = new google.maps.places.Autocomplete(document.getElementById('autocomplete'));

        autocomplete.addListener('place_changed', () => {
          var place = autocomplete.getPlace();

          if (!place.geometry) {
            return;
          }

          this.$emit('input', {
            latitude: place.geometry.location.lat(),
            longitude: place.geometry.location.lng(),
            zoom: this.value.zoom
          });
        });

        this.autoComplete();
      });
    },
    methods: {
      autoComplete () {
        GoogleMapsLoader.load((google) => {
          if (!this.value.latitude || !this.value.longitude) {
            this.markerAddress = '';
            return;
          }

          const url = '//maps.googleapis.com/maps/api/geocode/json?latlng=' + this.value.latitude + ',' + this.value.longitude + '&sensor=true&language=no';

          getJSON(url, (err, data) => {
            this.markerAddress = (data.status == 'OK') ? data.results[0].formatted_address : '';
          });
        });
      }
    },
    watch: {
      value (data, oldValue) {
        if(data.latitude === oldValue.latitude && data.longitude === oldValue.longitude) {
          return;
        }
        this.autoComplete()
      }
    }
  }


</script>



